body {
  margin: 0;
  position: relative;
  color: white;
  background: #1f1f1f;
  overflow-y: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  font-family: monospace;
}

html {
  touch-action: none;
}

html, body, #root {
  width: 100%;
  height: 100%;
}

.calculator {
  width: calc(100% - env(safe-area-inset-right) - env(safe-area-inset-left));
  height: calc(100% - env(safe-area-inset-bottom) - env(safe-area-inset-top));
  margin-left: env(safe-area-inset-left);
  margin-top: env(safe-area-inset-top);
  display: grid;
  grid-template-columnsc: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 0px 10px;
  grid-auto-flow: row;
  grid-template-areas:
    'calculator-top calculator-top calculator-top calculator-top'
    'calculator-top calculator-top calculator-top calculator-top'
    'calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-buttons calculator-buttons calculator-buttons calculator-buttons';
}

.calculator-top {
  grid-area: calculator-top;
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.calculator-stack {
  width: 100%;
  color: #c5c5c5;
  overflow-y: auto;
  padding-right: 10px;
  margin-top: auto;
}

.calculator-buffer {
  font-size: 3em;
  padding-right: 10px;
}

.calculator-buttons {
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-area: calculator-buttons;
  display: grid;
  gap: 3px 3px;
}

.calculator-extra-buttons {
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-area: calculator-extra-buttons;
  display: none;
  gap: 3px 3px;
}

@media (orientation: landscape) {
  .calculator {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
    'calculator-top calculator-top calculator-top calculator-top calculator-top calculator-top calculator-top'
    'calculator-top calculator-top calculator-top calculator-top calculator-top calculator-top calculator-top'
    'calculator-extra-buttons calculator-extra-buttons calculator-extra-buttons calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-extra-buttons calculator-extra-buttons calculator-extra-buttons calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-extra-buttons calculator-extra-buttons calculator-extra-buttons calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-extra-buttons calculator-extra-buttons calculator-extra-buttons calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-extra-buttons calculator-extra-buttons calculator-extra-buttons calculator-buttons calculator-buttons calculator-buttons calculator-buttons'
    'calculator-extra-buttons calculator-extra-buttons calculator-extra-buttons calculator-buttons calculator-buttons calculator-buttons calculator-buttons';
  }

  .calculator-extra-buttons {
    display: grid;
  }
}

.calculator-button {
  padding: 0;
  font-size: 2em;
  color: white;
  border: none;
  background: #131313;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.calculator-button-dark {
  background: #060606;
}
.calculator-button-light {
  background: #2c2c2c;
}

.calculator-button:hover {
  background: #4b4b4b;
}

.calculator-button:active, .calculator-button-active {
  background: #5f6164;
}

sup, sub {
  font-size: 70%;
}

.version {
  position: fixed;
  color: #5f6164;
  top: 5px;
  left: 5px;
}

.text-decoration-overline {
  text-decoration: overline;
  text-decoration-thickness: from-font;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #686868;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
